.compare{
	padding-bottom: 80px;
	&__container{
		display: flex;
		flex-wrap: wrap;
	}
	&-info{
		width: 330px;
		position: relative;
		&__fixed{
			height: 525px;
			width: 100%;
			padding-top: 93px;
			.text-accordion__info{
				width: 290px;
				&::before{
					content: none;
				}
			}
		}
		ul{
			li{
				position: relative;
				z-index: 30;
				font-weight: 500;
				font-size: 16px;
				line-height: 135%;
				color: #4F5050;
				padding: 12px 20px;
				border-bottom: 1px solid #D8DFE2;
				border-top: 1px solid transparent;
				margin: -1px;
				height: auto;
				&:nth-child(1){
					border-top: 1px solid transparent;
				}
			}
		}
	}
	&-items{
		width: calc(100% - 330px);
		&__container{
			display: flex;
			justify-content: flex-start;
			flex-wrap: wrap;
			position: relative;
		}
		&__links{
			width: 100%;
			display: flex;
			justify-content: space-between;
			padding: 20px 0;
			a{
				display: flex;
				align-items: center;
				font-size: 14px;
				line-height: 1.2;
				.icon{
					margin-left: 15px;
					transition: .3s;
					width: 25px;
					height: 25px;
				}
			}
			.sbmt--red{
				.icon{
					fill: #fff;
				}
				&:hover{
					.icon{
						fill: #EF4949;
					}
				}
			}
			.btn--gray{
				.icon{
					fill: #546062;
				}
				&:hover{
					.icon{
						fill: #fff;
					}
				}
			}
		}
	}
	&-item{
		border: 1px solid #CFD5D5;
		margin-left: -1px;
		width: 50%;
		max-width: 535px;
		position: relative;
		float: left;
		.delete{
			cursor: pointer;
			width: 25px;
			height: 25px;
			position: absolute;
			right: 10px;
			top: 10px;
			background-image: url(../images/svg/close-red.svg);
			background-position: center;
			background-size: contain;
			background-repeat: no-repeat;
			z-index: 99;

		}
		&__img{
			padding: 20px;
			height: 250px;
			width: 100%;
			position: relative;
			.bg{
				position: absolute;
				left: 20px;
				width: calc(100% - 40px);
				height: calc(100% - 40px);
				top: 20px;
				background-position: center;
				background-repeat: no-repeat;
				background-size: contain;
			}
		}
		&__text{
			background-color: #F8FBFB;
			padding: 9px 20px;
			width: 100%;
		}
		&__info{
			background-color: #F8FBFB;
			width: 100%;
			ul{
				li{
					border: 1px solid #D8DFE2;
					padding: 12px 20px;
					font-size: 16px;
					line-height: 135%;
					color: #4F5050;
					margin: -1px;
					position: relative;
					z-index: 1;
					height: auto;
					background-position: 20px 15px;
					background-repeat: no-repeat;
				}
				.superior{
					border: 1px solid #FFA782;
					background-color: #fff;
					z-index: 30;
				}
				.yes{
					background-image: url(../images/close-red.svg);
					padding-left: 60px;
				}
				.no{
					background-image: url(../images/check-green.svg);
					padding-left: 60px;
				}
			}
			&-link{
				display: flex;
				justify-content: center;
				padding: 25px 10px;
				a{
					display: flex;
					align-items: center;
					font-size: 14px;
					line-height: 1.2;
					.icon{
						margin-left: 15px;
						fill: #EF4949;
						transition: .3s;
						width: 25px;
						height: 25px;
					}
					&:hover{
						.icon{
							fill: #fff;
						}
					}
				}
			}
		}
		&__type{
			font-weight: 300;
			font-size: 14px;
			line-height: 135%;
			color: #000000;
			margin-bottom: 10px;
		}
		&__name{
			font-weight: 600;
			font-size: 20px;
			line-height: 125%;
			color: #333131;
			margin-bottom: 15px;
			height: 60px;

		}
		&__price{
			font-weight: 600;
			font-size: 19px;
			line-height: 120%;
			color: #000000;
			margin-bottom: 38px;
		}
	}
}

.compare-items__container.child-3{
	.compare-item{
		width: 33.33%;
	}
}
.compare-items__container{
	overflow-x: auto;
	position: relative;
	padding-bottom: 50px;
	.mCustomScrollBox {
		width: 100%;
	}
	.mCSB_horizontal.mCSB_inside>.mCSB_container{
		margin-bottom: 0;
	}
	.mCSB_scrollTools.mCSB_scrollTools_horizontal{
		z-index: 50;
		opacity: 1;
		position: fixed;
		bottom: 16px;
		top: auto;
		left: auto;
	}
	.mCSB_scrollTools .mCSB_draggerRail{
		background-color: rgb(242, 242, 242);
		height: 30px;
		width: calc(100% - 2px);
		
		box-sizing: content-box;
	}
	.mCSB_scrollTools.nofixed .mCSB_draggerRail {
		margin-left: 1px;
	}
	.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar{
		background-color: #bbbbbb !important;
		height: 20px;
		margin: 7.5px;
	}
	.mCS-inset-2-dark.mCSB_scrollTools .mCSB_dragger:hover .mCSB_dragger_bar{
		background-color: #a09f9f !important;
	}
	.mCSB_scrollTools .mCSB_draggerRail,
	.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar{
		border-radius: 0;
	}
	.scroll--active{
		//flex-wrap: nowrap;
		//display: flex;
		padding-bottom: 30px;
		width: 100%;
	}
	.compare-item{
		display: flex;
		flex-wrap: wrap;
		&:nth-child(1){
			margin-left: 1px;
		}
	}
	#mCSB_1_scrollbar_horizontal.nofixed{
		position: absolute;
		bottom: 20px;
		right: 2px !important;
		width: calc(100% - 2px) !important;
	}


}

.compare-items__container.scroll{
	&::before,
	&::after {
		width: 1px;
		height: calc(100% - 80px);
		background-color: #fff;
		position: absolute;
		top: 0;
		content: '';
		z-index: 20;
	}
	&::before {
		left: 0;
		border-right: 1px solid #cfd5d5;
	}

	&::after {
		right: 0;
		border-left: 1px solid #cfd5d5;
	}
	.compare-item{
		width: 357px;
	}
}